import React, { useState, useEffect } from 'react';
import JSEncrypt from 'jsencrypt';
import PasswordInput from './PasswordInput';
import { deriveKey, encryptAES, decryptAES } from '../utils/crypto';

function DemoTab() {
  // State for key management
  const [keySize, setKeySize] = useState('1024');
  const [passphrase, setPassphrase] = useState('');
  const [publicKey, setPublicKey] = useState('');
  const [encryptedPrivateKey, setEncryptedPrivateKey] = useState(null);
  const [salt, setSalt] = useState(null);
  const [iv, setIv] = useState(null);
  const [jsEncrypt, setJsEncrypt] = useState(null);
  const [showKeyManagement, setShowKeyManagement] = useState(false);
  const [showPrivateKey, setShowPrivateKey] = useState(false);
  const [privateKey, setPrivateKey] = useState('');

  // State for passphrase change
  const [currentPassphrase, setCurrentPassphrase] = useState('');
  const [newPassphrase, setNewPassphrase] = useState('');
  const [passphraseMessage, setPassphraseMessage] = useState('');

  // State for encryption/decryption
  const [plainText, setPlainText] = useState(
    'y l tin nhn mt. RSA bo v thng tin ca bn!'
  );
  const [encryptedText, setEncryptedText] = useState('Cha c d liu m ha');
  const [cipherText, setCipherText] = useState('');
  const [decryptedText, setDecryptedText] = useState('Cha c d liu gii m');
  const [decryptPassphrase, setDecryptPassphrase] = useState('');

  // Initialize JSEncrypt instance
  useEffect(() => {
    setJsEncrypt(new JSEncrypt());
  }, []);

  // Generate RSA key pair
  const generateKeys = async () => {
    try {
      console.log('Bt u to kha...');
      const passphraseValue = passphrase.trim();
      console.log('Passphrase:', passphraseValue ? ' nhp' : 'Cha nhp');

      // Validate passphrase
      if (!passphraseValue) {
        alert('Vui lng nhp passphrase!');
        return;
      }

      console.log('Bt u to cp kha RSA...');

      // Generate RSA key pair
      const keySizeValue = parseInt(keySize);
      console.log('Kch thc kha:', keySizeValue);

      // Create new JSEncrypt instance
      const newJsEncrypt = new JSEncrypt();
      newJsEncrypt.getKey(keySizeValue);
      console.log(' to cp kha RSA');

      const publicKeyValue = newJsEncrypt.getPublicKey();
      const privateKeyValue = newJsEncrypt.getPrivateKey();
      console.log(
        ' ly kha cng khai v ring t:',
        publicKeyValue ? 'C' : 'Khng',
        privateKeyValue ? 'C' : 'Khng'
      );

      if (!publicKeyValue || !privateKeyValue) {
        throw new Error('Khng th to cp kha RSA');
      }

      console.log('Bt u to kha t passphrase...');
      // Derive key from passphrase
      const derivedKeyData = await deriveKey(passphraseValue);
      const saltValue = derivedKeyData.salt;
      console.log(' to kha t passphrase, bt u m ha kha ring t...');

      // Encrypt private key with derived key
      const encryptionResult = await encryptAES(
        privateKeyValue,
        derivedKeyData.key
      );
      const encryptedPrivateKeyValue = encryptionResult.encryptedData;
      const ivValue = encryptionResult.iv;
      console.log(' m ha kha ring t thnh cng');

      // Update state
      setJsEncrypt(newJsEncrypt);
      setPublicKey(publicKeyValue);
      setPrivateKey(privateKeyValue);
      setEncryptedPrivateKey(encryptedPrivateKeyValue);
      setSalt(saltValue);
      setIv(ivValue);
      setShowKeyManagement(true);
      setDecryptPassphrase(passphraseValue);

      console.log('Hon tt qu trnh to kha');
    } catch (error) {
      console.error('Error generating keys:', error);
      alert(' xy ra li khi to kha: ' + error.message);
    }
  };

  // Toggle private key visibility
  const togglePrivateKeyVisibility = () => {
    setShowPrivateKey(!showPrivateKey);
  };

  // Change passphrase
  const changePassphrase = async () => {
    try {
      const currentPassphraseValue = currentPassphrase.trim();
      const newPassphraseValue = newPassphrase.trim();

      // Validate passphrases
      if (!currentPassphraseValue || !newPassphraseValue) {
        setPassphraseMessage(
          '<div class="warning-box">Vui lng nhp c passphrase hin ti v passphrase mi!</div>'
        );
        return;
      }

      // Verify current passphrase
      try {
        // Derive key from current passphrase
        const derivedKeyData = await deriveKey(currentPassphraseValue, salt);

        // Try to decrypt the private key
        const decryptedPrivateKey = await decryptAES(
          encryptedPrivateKey,
          derivedKeyData.key,
          iv
        );

        // If successful, encrypt with new passphrase
        const newDerivedKeyData = await deriveKey(newPassphraseValue);
        const newEncryptionResult = await encryptAES(
          decryptedPrivateKey,
          newDerivedKeyData.key
        );

        // Update the stored values
        setEncryptedPrivateKey(newEncryptionResult.encryptedData);
        setIv(newEncryptionResult.iv);
        setSalt(newDerivedKeyData.salt);
        setDecryptPassphrase(newPassphraseValue);

        // Show success message
        setPassphraseMessage(
          '<div class="success-box">Passphrase  c thay i thnh cng!</div>'
        );

        // Clear passphrase fields
        setCurrentPassphrase('');
        setNewPassphrase('');
      } catch (error) {
        setPassphraseMessage(
          '<div class="warning-box">Passphrase hin ti khng ng!</div>'
        );
      }
    } catch (error) {
      console.error('Error changing passphrase:', error);
      setPassphraseMessage(
        '<div class="warning-box">Li khi thay i passphrase: ' +
          error.message +
          '</div>'
      );
    }
  };

  // Encrypt message
  const encryptMessage = () => {
    if (!jsEncrypt || !jsEncrypt.getPublicKey()) {
      alert('Vui lng to cp kha trc!');
      return;
    }

    if (!plainText) {
      alert('Vui lng nhp tin nhn cn m ha!');
      return;
    }

    const encrypted = jsEncrypt.encrypt(plainText);
    setEncryptedText(encrypted);
    setCipherText(encrypted);
  };

  // Decrypt message
  const decryptMessage = async () => {
    try {
      const cipherTextValue = cipherText;
      const decryptPassphraseValue = decryptPassphrase.trim();

      if (!encryptedPrivateKey || !salt || !iv) {
        alert('Vui lng to cp kha trc!');
        return;
      }

      if (!cipherTextValue) {
        alert('Vui lng nhp tin nhn  m ha!');
        return;
      }

      if (!decryptPassphraseValue) {
        alert('Vui lng nhp passphrase  gii m!');
        return;
      }

      // Derive key from passphrase
      const derivedKeyData = await deriveKey(decryptPassphraseValue, salt);

      // Decrypt the private key
      try {
        const decryptedPrivateKey = await decryptAES(
          encryptedPrivateKey,
          derivedKeyData.key,
          iv
        );

        // Set the private key in JSEncrypt
        jsEncrypt.setPrivateKey(decryptedPrivateKey);

        // Decrypt the message
        const decrypted = jsEncrypt.decrypt(cipherTextValue);

        if (decrypted === false) {
          setDecryptedText(
            'Khng th gii m. Vui lng kim tra tin nhn  m ha.'
          );
        } else {
          setDecryptedText(decrypted);
        }
      } catch (error) {
        setDecryptedText(
          'Khng th gii m. Passphrase khng ng hoc d liu  b hng.'
        );
      }
    } catch (error) {
      console.error('Error decrypting message:', error);
      setDecryptedText('Li khi gii m: ' + error.message);
    }
  };

  return (
    <div>
      <div className="section">
        <h2>Bc 1: Thit lp kha v mt khu</h2>

        <div className="two-columns">
          <div className="column">
            <h3>La chn  di kha:</h3>
            <select
              value={keySize}
              onChange={(e) => setKeySize(e.target.value)}
            >
              <option value="512">512 bit (demo - khng an ton)</option>
              <option value="1024">1024 bit (demo - khng an ton)</option>
              <option value="2048">2048 bit ( xut cho thc t)</option>
            </select>

            <h3>Nhp passphrase bo v kha ring t:</h3>
            <PasswordInput
              value={passphrase}
              onChange={(e) => setPassphrase(e.target.value)}
              placeholder="Nhp passphrase  bo v kha ring t ca bn..."
              id="passphrase"
            />

            <button className="button-primary" onClick={generateKeys}>
              To cp kha mi
            </button>
          </div>
          <div className="column">
            <div className="info-box">
              <p>
                <strong>Lu :</strong> Trong demo ny, chng ta s dng:
              </p>
              <ul>
                <li>PBKDF2  to kha t passphrase</li>
                <li>AES-GCM  m ha kha ring t RSA</li>
                <li>Passphrase bo v kha ring t khi k tn cng</li>
              </ul>
              <p>
                <strong>
                  Tuyt i khng  l kha ring t hoc passphrase!
                </strong>
              </p>
            </div>
          </div>
        </div>

        {showKeyManagement && (
          <div>
            <div className="success-box">
              <p>
                Cp kha  c to thnh cng v kha ring t  c m ha!
              </p>
            </div>

            <div className="two-columns">
              <div className="column">
                <h3>Kha cng khai (Public Key):</h3>
                <div className="key-display">{publicKey || 'Cha c kha'}</div>
              </div>
              <div className="column">
                <h3>Kha ring t (Private Key):</h3>
                <div className="key-display">
                   c m ha bng passphrase
                </div>

                <button
                  className="button-secondary"
                  onClick={togglePrivateKeyVisibility}
                >
                  {showPrivateKey
                    ? 'n kha ring t'
                    : 'Hin th kha ring t'}
                </button>
                {showPrivateKey && (
                  <div className="key-display">
                    {privateKey ||
                      'Kha ring t s hin th  y sau khi xc thc'}
                  </div>
                )}
              </div>
            </div>
          </div>
        )}

        {/* Passphrase Change Section */}
        <div className="info-box" style={{ marginTop: '20px' }}>
          <h3>i passphrase</h3>
          <p>
            Bn c th i passphrase m khng lm mt kh nng gii m cc tin
            nhn c.
          </p>

          <div className="two-columns">
            <div className="column">
              <h4>Passphrase hin ti:</h4>
              <PasswordInput
                value={currentPassphrase}
                onChange={(e) => setCurrentPassphrase(e.target.value)}
                placeholder="Nhp passphrase hin ti..."
                id="current-passphrase"
              />
            </div>
            <div className="column">
              <h4>Passphrase mi:</h4>
              <PasswordInput
                value={newPassphrase}
                onChange={(e) => setNewPassphrase(e.target.value)}
                placeholder="Nhp passphrase mi..."
                id="new-passphrase"
              />
            </div>
          </div>

          <button className="button-warning" onClick={changePassphrase}>
            i passphrase
          </button>
          {passphraseMessage && (
            <div dangerouslySetInnerHTML={{ __html: passphraseMessage }} />
          )}
        </div>
      </div>

      <div className="section">
        <h2>Bc 2: M ha tin nhn</h2>
        <p>Nhp tin nhn v s dng kha cng khai  m ha.</p>

        <div className="two-columns">
          <div className="column">
            <h3>Tin nhn gc:</h3>
            <textarea
              value={plainText}
              onChange={(e) => setPlainText(e.target.value)}
              placeholder="Nhp tin nhn cn m ha..."
            />
            <button className="button-primary" onClick={encryptMessage}>
              M ha tin nhn
            </button>
          </div>
          <div className="column">
            <h3>Tin nhn  m ha:</h3>
            <div className="key-display">{encryptedText}</div>
          </div>
        </div>
      </div>

      <div className="section">
        <h2>Bc 3: Gii m tin nhn</h2>
        <p>S dng kha ring t  gii m tin nhn  m ha.</p>

        <div className="two-columns">
          <div className="column">
            <h3>Tin nhn  m ha:</h3>
            <textarea
              value={cipherText}
              onChange={(e) => setCipherText(e.target.value)}
              placeholder="D liu  m ha..."
            />

            <h3>Xc thc bng passphrase:</h3>
            <PasswordInput
              value={decryptPassphrase}
              onChange={(e) => setDecryptPassphrase(e.target.value)}
              placeholder="Nhp passphrase  gii m..."
              id="decrypt-passphrase"
            />

            <button className="button-primary" onClick={decryptMessage}>
              Gii m tin nhn
            </button>
          </div>
          <div className="column">
            <h3>Tin nhn  gii m:</h3>
            <div className="key-display">{decryptedText}</div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default DemoTab;
